@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
.header{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 35vh;
    width: 100%;
}

.title {
    font-size: 7vw;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
}

@media (max-width: 900px) {
    .header {
      justify-content: space-evenly;
      height: 25vh;
    }
    .title {
      font-size: 11vw;
    }
}

.inputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
}
  
.search {
    width: 43%;
}
  
.select {
    width: 43%;
}
.meanings {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 55vh;
    border: 10px solid rgb(105, 105, 105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}
  
.subTitle {
    font-size: 5vw;
    font-family: "Montserrat", sans-serif;
}
  
@media (max-width: 900px) {
    .meanings {
      overflow: scroll;
      overflow-x: hidden;
      height: 60vh;
    }
}
.singleMean {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

